.sample {
   color: blue;
}
.fx {
   display: flex;
}

@keyframes opacityTransition {
   from {
      opacity: 0;
   }
   to {
      opacity: 1;
   }
}

.navbarlist {
   transition: 0.2s;
   padding: 0px 5px;
   margin: 0px 5px;
   background-color: rgb(0 128 128 / var(--tw-text-opacity));
   color: #ffffff;
}

.navbarlist-active {
   transition: 0.2s;
   padding: 0px 5px;
   margin: 0px 5px;
   color: rgb(0 128 128 / var(--tw-text-opacity));
   background-color: #ffffff;
   border-radius: 7px;
}

.navbarlist:hover {
   color: rgb(0, 128, 128);
}

.navbarlist::before {
   content: "";
   width: 0%;
   height: 100%;
   position: absolute;
   z-index: -2;
   opacity: 0;
   border-radius: 7px;
   left: 0;
   top: 50%;
   transition: 0.2s ease-out;
   transform: scale(0);
   padding: 5px 10px;
}

.navbarlist:hover::before {
   content: "";
   height: 100%;
   width: 100%;
   background-color: #ffffff;
   opacity: 1;
   top: 0%;
   transform: scale(1);
}

.navbarlink {
   padding: 5px 10px;
}

.active {
   background-color: white;
   color: rgb(0, 128, 128);
   border-radius: 7px;
}

.navbarbackground {
   z-index: 100;
}

.content {
   animation: slide-in-from-left 1s cubic-bezier(0.075, 0.82, 0.165, 1) forwards;
}

.contentExit {
   animation: slide-out-from-left 1s ease forwards;
}

.showImage {
   animation: pop-in 0.5s cubic-bezier(0.68, -0.55, 0.65, 0.52) forwards;
}

@keyframes pop-in {
   0% {
      transform: scale(0);
      opacity: 0;
   }
   80% {
      transform: scale(1.1);
      opacity: 1;
   }
   100% {
      transform: scale(1);
   }
}

@keyframes slide-in-from-left {
   0% {
      opacity: 0;
      transform: translateX(-30%);
   }
   100% {
      opacity: 1;
      transform: translateX(0);
   }
}

@keyframes slide-out-from-left {
   0% {
      opacity: 1;
      transform: scale(1);
   }
   100% {
      opacity: 0;
      transform: scale(0);
   }
}
